.App {
  background-color:#241c2c;
  margin: 0;
  padding: 0;
  color: #bed8da;
}

::-webkit-scrollbar {
  width: 8px;
  padding-right: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(64, 59, 102); 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(52, 46, 83); 
}
